  background: url(./static/btndropdown.png) no-repeat center left;
  padding: 5px;
  position: relative;
  left: 130px;
  top: -8px; }

@media (min-width: 320px) and (max-width: 767px) {
  .callBackSection, .callSection {
    width: 320px; }
  .callBackSection__title, .callSection__title {
    padding-left: 12px; }
  .callBackSection__btnClose, .callSection__btnClose {
    padding: 24px 0 32px 16px; }
  .callSection__inner {
    padding: 24px 16px 16px 16px; }
  .callBackSection__inner {
    padding: 24px 16px 75px 16px; } }

@media (min-width: 768px) and (max-width: 1439px) {
  .callBackSection, .callSection {
    width: 440px; }
