@font-face {
  font-family: 'TTLakes';
  src: url(./fonts/TTLakes-Regular.eot);
  src: local(":relaxed:"), url(./fonts/TTLakes-Regular.woff) format("woff"), url(./fonts/TTLakes-Regular.ttf) format("truetype");
  src: url(./fonts/TTLakes-Bold.eot);
  src: local(":relaxed:"), url(./fonts/TTLakes-Bold.woff) format("woff"), url(./fonts/TTLakes-Bold.ttf) format("truetype");
  src: url(./fonts/TTLakes-Medium.eot);
  src: local(":relaxed:"), url(./fonts/TTLakes-Medium.woff) format("woff"), url(./fonts/TTLakes-Medium.ttf) format("truetype");
  font-weight: normal;
  font-style: normal; }

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

body {
  font-family: "TT Lakes"; }

.general {
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
  box-shadow: 0 0 15px 1px black; }
  .general .callBackSection, .general .callSection {
    display: none; }

.mainSection {
  background: #F8F8F8;
  width: 100%; }

.hidden {
  opacity: 0.1;
  transition-duration: 0.5s;
  transition-timing-function: ease-in, ease, linear; }

.callBackSection.popup {
  display: none; }

.callSection.popup {
  display: none; }
